
.updateOrderForm{
     margin:5vmax 0;
    padding:3vmax;
     background-color: white;
   
}

.updateOrderForm>div{
    display: flex;
    width:100%;
    align-items: center;
}


.updateOrderForm>div>select{
    padding:1vmax 4vmax;
    padding-right:1vmax;
    width:100%;
    margin:2rem 0;
    box-sizing:border-box;
    box-shadow: -1px 0px 7px #0E185F, 2px 1px 4px #0E185F;
    border:1px solid rgba(0,0,0,0.267);
    border-radius: 4px;
    font:300 0.9vmax cursive;
    outline:none;
    background-color:rgba(233,0,100,0.5)
}

.updateOrderForm>div>svg{
    position:absolute;
    transform: translateX(1vmax);
    font-size:1.6vmax;
    color:rgba(0,0,0,0.623);
}

@media screen and (max-width:600px){
    .newProductContainer{
        background-color:rgb(255,255,255);
    }
    
    .updateOrderForm{
        padding: 5vmax;
    }
    
    
    .updateOrderForm>div>select
    {
        padding:2.5vmax 5vmax;
        font:300 1.7vmax cursive;
    }
    
    .updateOrderForm>div>svg{
        font-size:2.8vmax;
    }
   
}


