.profileContainer{
    display:flex;
    height:100vh;
    width:100vw;
    position:fixed;
    top:0%;
    left:0%;
    max-width: 100%;
    /* background-color: white; */
    background-color:rgb(3, 0, 28);
}
.profileContainer>div{
    display:flex;
    height:100vh;
    width:100vw;
    max-width:100%;
    flex-direction:column;
    justify-content: center;
     align-items:center; 
}
.profileContainer>div:first-child>h1{
    color:#0ef;
    font:500 2.2vmax "Acme";
    transform:translateX(-10vmax) translateY(-2vmax);
}



.profileContainer>div:first-child>img{
    width: 20vmax;
    border-radius:100%;
    transition:all 0.5s;
}
.profileContainer>div:first-child>img:hover{
    transform:scale(1.05);
}
.profileContainer>div:first-child>a{
    border:none;
    background-image: linear-gradient(to left bottom, #b9f3fc, #8ce3ff, #7aceff, #98b1ff, #cb8aff);
    font:400 1vmax "Roboto";
    color:rgb(3, 0, 28);
    text-decoration: none;
    padding:0.5vmax;
    width:30%;
    margin:4vmax;
    text-align: 4vmax;
    transition:all 0.5s;
}
.profileContainer>div:first-child>a:hover{
    background-color: rgb(204,78,56);
}
.profileContainer>div:last-child{
    justify-content:space-evenly;
    align-items:flex-start;
    padding:5vmax;
    box-sizing:border-box;
}
.profileContainer>div:last-child>div>h4{
    color:#FF48B0;
    font:800 1.8vmax "Roboto";
}
.profileContainer>div:last-child>div>p{
    color:#FF6700;
    font:400 1vmax cursive;
    margin:0.2vmax;
    border-bottom: 1px solid #0ef;
    border-width: 100%;
    
}
.profileContainer>div:last-child>div:last-child{
    display:flex;
    flex-direction:column;
    width:60%;
}
.profileContainer>div:last-child>div:last-child>a{
    border:none;
    background-image: linear-gradient(to left bottom, #b9f3fc, #8ce3ff, #7aceff, #98b1ff, #cb8aff);
    padding:0.5vmax;
    text-align: center;
    text-decoration:none;
    font:400 1vmax "Roboto";
    color:rgb(3, 0, 28);
    transition: all 0.5s;
    margin:1vmax 0;
    border-radius: 5px;
}

.profileContainer>div:last-child>div:last-child>a:hover{
    background-image: linear-gradient(to right top, #051937, #332457, #6c266a, #a71c6b, #db1a5a);
}
@media screen and (max-width:600px){
    .profileContainer{
        flex-direction:column;
    }
    .profileContainer>div:first-child>h1{
        font:500 3.2vmax "Roboto";
        transform:translateY(-2vmax);
    }
    .profileContainer>div:last-child{
        text-align:center;
        align-items:center;
    }
    .profileContainer>div:last-child>div>h4{
        font:400 2.5vmax "Roboto";
    }
    .profileContainer>div:last-child>div>p{
        font:400 2vmax cursive;
        margin:0.5vmax;
    }
    .profileContainer>div:last-child>div:last-child>a{
        font:400 1.8vmax "Roboto";
        padding:1vmax;
        margin:2vmax 0;
    }
    
}