.newProductContainer{
    width:100%;
    box-sizing:border-box;
    background-color: white;
    border-left: 1px solid rgba(0,0,0,0.158);
    display:flex;
    flex-direction:column;
    height:100vh;
}
.newProductContainer h1{
    color:rgba(0,0,0,0.733);
    font:300 2rem "Roboto";
    text-align:center;
}

.createProductForm{
    display: flex;
    flex-direction: column;
    align-items:center;
    margin:auto;
    padding:3vmax;
    justify-content: space-evenly;
    height:70%;
    width:40vh;
    background-color: white;
    box-shadow: 0 0 10px rgba(0,0,0,0.267);
}

.createProductForm>div{
    display: flex;
    width:100%;
    align-items: center;
}

.createProductForm>div>input,
.createProductForm>div>select,
.createProductForm>div>textArea{
    padding:1vmax 4vmax;
    padding-right:1vmax;
    width:100%;
    box-sizing:border-box;
    box-shadow: -1px 0px 7px #0E185F, 2px 1px 4px #0E185F;
    border:1px solid rgba(0,0,0,0.267);
    border-radius: 4px;
    font:300 0.9vmax cursive;
    outline:none;
    background-color:rgba(233,0,100,0.5)
}

.createProductForm>div>svg{
    position:absolute;
    transform: translateX(1vmax);
    font-size:1.6vmax;
    color:rgba(0,0,0,0.623);
}

#createProductFormFile>input{
    display: flex;
    padding:0%;
}
#createProductFormFile>input::file-selector-button{
    background-color: rgba(0,0,0,0);
    cursor:pointer;
    width:100%;
    height:5vh;
     z-index: 2; 
    margin: 0%;
    border: none;
    font:400 0.8vmax cursive;
    transition:all 0.5s;
    padding:0 1vmax;
    /* border:1px solid rgba(0,0,0,0.267);
    color: rgba(0,0,0,0.623); */
   
}
#createProductFormFile>input::file-selector-button:hover{
    background-color: rgba(0,0,0,0);
} 
#createProductFormImage{
    width:100%;
    overflow:auto;
}
#createProductFormImage>img{
    width: 3vmax;
    margin:0 0.5vmax;
    
}
#createProductBtn{
    border:none;
    background-color: #060047;
    color: white;
    font: 300 0.9vmax "Roboto";
    width:100%;
    padding:0.8vmax;
    cursor:pointer;
    transition:all 0.5s;
    border-radius:4px;
    outline:none;
    box-shadow:0 2px 5px rgba(0,0,0,0.219);
}

#createProductBtn:hover{
    background-color: #0E185F;
}
@media screen and (max-width:600px){
    .newProductContainer{
        background-color:rgb(255,255,255);
    }
    
    .createProductForm{
        padding: 5vmax;
    }
    
    .createProductForm>div>input,
    .createProductForm>div>select,
    .createProductForm>div>textarea{
        padding:2.5vmax 5vmax;
        font:300 1.7vmax cursive;
    }
    
    .createProductForm>div>svg{
        font:2.8vmax;
    }
   
    #createProductFormFile>img{
        width:8vmax;
        border-radius:100%;
    }
    #createProductFormFile>input::file-selector-button{
        height:7vh;
        font:400 1.8vmax cursive;
    };
    #createProductBtn{
        font:300 1.9vmax "Roboto";
        padding:1.8vmax;
    }
}


