.dashboard{
    width:100vw;
    max-width:100%;
    display:grid;
    grid-template-columns:1fr 5fr;
    position:absolute;
}
.dashboardContainer{
    border-left:1px solid rgba(0,0,0,0.13);
    background-color: rgb(255,255,255);
    padding:3rem 0;
}
.dashboardContainer>h1{
    color:rgba(0,0,0,0.733);
    font:300 2rem "Roboto";
    text-align:center;
    width:50%;
    padding: 1.5rem;
    margin:auto;
}
.dashboardSummary{
    margin:2rem 0;
}
.dashboardSummary>div{
    display:flex;
    justify-content: center;
    background-color:white;
}
.dashboardSummary>div>p{
    background-color: rgba(70,117,218,0.932);
    color:white;
    padding:1.5rem;
    width:100%;
    margin:0 2rem;
    text-align:center;
    font:300 1.3rem "Roboto";
}
.dashboardSummaryBox2>a{
    color:rgb(255,255,255);
    font:300 2rem "Roboto";
    text-align:center;
    background-image: linear-gradient(to right top, #370529, #5f0a42, #8a0e59, #b9116f, #eb1282);
    justify-content:center;
    align-items:center;
    flex-direction:column;
    text-decoration:none;
    padding:1.5rem;
    border-radius:100%;
    width:10vmax;
    height:10vmax;
    margin:2rem,

}
.dashboardSummaryBox2>a:first-child{
    background-image: linear-gradient(to right top, #051937, #004d7a, #008793, #00bf72, #a8eb12);
    color:rgb(255,255,255);
}
.dashboardSummaryBox2>a:last-child{
    background-image: linear-gradient(to right top, #051937, #5f2e61, #ba4162, #f1793f, #ebcc12);
    color:rgb(255,255,255);
}
.lineChart{
    width:80%;
    margin:auto;
}
.doughnutChart{
    width:30vmax;
    margin:auto;
}
@media screen and (max-width:600px){
    .dashboard{
        grid-template-columns:1fr;
    }
    .dashboardContainer{
        border-left:none;
    }
    .dashboardSummary>div>p{
        margin:0;
    }
    .dashboardSummaryBox2>a{
        padding:0.5rem;
        margin:1rem;
        font:300 0.9rem "Roboto";
    }
}