.myOrdersPage{
    width:100vw;
    max-width:100%;
    padding:0 7vmax;
    box-sizing:border-box;
    background-color: rgb(3, 0, 28) ;
    position:fixed;
    left:0;
    top:0;
    height:100vh;
    display:flex;
    flex-direction:column;
    margin-top:40px;
   
    
}
#myOrdersHeading{
    text-align:center;
    font:400 1.2vmax "Roboto";
    padding:0.5vmax;
    box-sizing:border-box;
    color:rgb(255,255,255);
    transition:all 0.5s;
    background-color: rgb(44,44,44);
}
.myOrdersTable div{
    font:300 1vmax "Roboto";
    color:#0ef;
    border:none;

}
.myOrdersTable a{
    color:#FF48B0;
    transition:all 0.5s;
}
.myOrdersTable a:hover{
    color:tomato;
}
.MuiDataGrid-columnHeader{
    background-image: linear-gradient(to left bottom, #b9f3fc, #8ce3ff, #7aceff, #98b1ff, #cb8aff);
    padding:1vmax !important;
}
.MuiDataGrid-columnHeader div{
    color:rgb(255,255,255);
    font:500 1.1vmax "Roboto" !important;

}
.MuiDataGrid-iconSeparator{
   display: none !important;
}

@media screen and(max-width:600px){
.myOrdersPage{
    padding:0;
    height:93vh;
}
#myOrdersHeading{
    font:400 2.2vmax "Roboto";
    padding:4vw;
}
.myOrdersTable div{
font:300 4vw "Roboto";
}
.MuiDataGrid-columnHeader{
    padding:20px !important;
}
}